// 기본 폰트 정의
body {
  min-width: 360px;
  font-family: $default_font;
}
// 웹접근성용 안내 문구 숨김
.blind {
  font-size: 0;
  position: absolute;
  text-indent: -9999px;
}
// input 기본 모양 설정
input {
  box-sizing: border-box;
  font-size: $font-16r;
}
// address 기본 모양 설정
address {
  font-style: normal;
  font-size: $font-16r;
}
html {
  font-family: $default-font;
}
// title text
.tit-h1 {
  font-size: $font-54r;
  font-weight: $b;
  line-height: $ln-h-w;
  color: $dark-blue;
}
.tit-h2 {
  font-size: $font-48r;
  font-weight: $b;
  line-height: $ln-h-w;
  color: $dark-blue;
}
.tit-h3 {
  font-size: $font-38r;
  font-weight: $b;
  line-height: $ln-h-w;
  color: $dark-blue;
}
.tit-h4 {
  font-size: $font-36r;
  font-weight: $md;
  line-height: $ln-h-w;
  color: $dark-blue;
}
.tit-h5 {
  font-size: $font-32r;
  font-weight: $md;
  line-height: $ln-h-w;
  color: $dark-blue;
}
.tit-h6 {
  font-size: $font-24r;
  font-weight: $md;
  line-height: $ln-h-w;
  color: $dark-blue;
}

// text
.txt-r {
  font-size: $font-16r;
  font-weight: $r;
  line-height: $ln-h-r;
  color: $dark-blue;
}
.txt-md {
  font-size: $font-16r;
  font-weight: $md;
  line-height: $ln-h-r;
  color: $dark-blue;
}
// label text
.lb-txt-1 {
  font-size: $font-18r;
  font-weight: $md;
  line-height: $ln-h-w;
  color: $dark-blue;
}
.lb-txt-2 {
  font-size: $font-16r;
  font-weight: $md;
  line-height: $ln-h-w;
  color: $dark-blue;
}

// font 반응형
@include t-l {
  .tit-h1 {
    font-size: $font-48r;
    line-height: $ln-h-r;
  }
  .tit-h2 {
    font-size: $font-38r;
    line-height: $ln-h-r;
  }
  .tit-h3 {
    font-size: $font-36r;
    line-height: $ln-h-r;
  }
  .tit-h4 {
    font-size: $font-32r;
    line-height: $ln-h-r;
  }
  .tit-h5 {
    font-size: $font-24r;
    line-height: $ln-h-r;
  }
  .tit-h6 {
    font-size: $font-20r;
    line-height: $ln-h-r;
  }
  .txt-r {
    font-size: $font-14r;
    line-height: $ln-h-n;
  }
  .txt-md {
    font-size: $font-14r;
    line-height: $ln-h-n;
  }
  .lb-txt-1 {
    font-size: $font-16r;
    line-height: $ln-h-r;
  }
  .lb-txt-2 {
    font-size: $font-14r;
    line-height: $ln-h-r;
  }
}
@include m-l {
  .tit-h1 {
    font-size: $font-38r;
    line-height: $ln-h-r;
  }
  .tit-h2 {
    font-size: $font-36r;
    line-height: $ln-h-r;
  }
  .tit-h3 {
    font-size: $font-32r;
    line-height: $ln-h-r;
  }
  .tit-h4 {
    font-size: $font-24r;
    line-height: $ln-h-r;
  }
  .tit-h5 {
    font-size: $font-20r;
    line-height: $ln-h-r;
  }
  .tit-h6 {
    font-size: $font-18r;
    line-height: $ln-h-r;
  }
  .txt-r {
    font-size: $font-12r;
    line-height: $ln-h-n;
  }
  .txt-md {
    font-size: $font-12r;
    line-height: $ln-h-n;
  }
  .lb-txt-1 {
    font-size: $font-14r;
    line-height: $ln-h-r;
  }
  .lb-txt-2 {
    font-size: $font-12r;
    line-height: $ln-h-r;
  }
}

// color
.royal-blue {
  background-color: $royal-blue;
}
.dark-blue {
  background-color: $dark-blue;
}
.grey {
  background-color: $grey;
}
.yellow {
  background-color: $yellow;
}
.tint-blue {
  background-color: $tint-blue;
}
.accent {
  background-color: $accent;
}
.light-yellow {
  background-color: $light-yellow;
}

// button
button {
  cursor: pointer;
}

.btn-ty01 {
  background-color: $yellow;
  transition: background-color 0.3s;
  color: #1b1c2b;
  border-radius: 40px;
}

.btn-ty01:hover {
  background-color: $yellow-hover;
}

.btn-ty02 {
  background-color: $dark-blue;
  transition: background-color 0.3s;
  color: $white;
  border-radius: 40px;
}

.btn-ty02:hover {
  background-color: $dark-blue-hover;
}